import React from 'react';
import Link from 'gatsby-link';
import vwDealershipBanner from "./static/images/vwDealershipBanner.png";
import vwDealershipImg1 from "./static/images/vwDealershipImg1.jpg";
import vwDealershipImg2 from "./static/images/vwDealershipImg2.jpg";
import vwDealershipImg3 from "./static/images/vwDealershipImg3.jpg";
import penguinImg from "./static/images/penguinImg.jpg";
import boscoImg from "./static/images/boscoImg.jpg";

const vwdealership = () => (
  <div className="fullBox">

    <div className="insideBanner vwDealershipBanner">
      <div className="bg vwDealership"></div>
      <div className="midWrapper">
            <div className="rightSection">
              <div className="box">
                <div className="vMid">
                  <img src={vwDealershipBanner} alt="" />
                </div>
              </div>
            </div>
            <div className="leftSection">
              <div className="box">
                <div className="vMid">
                  <h1>VOLKSWAGEN</h1>
                  <h2>Dealership Website</h2>
                  <p>Client : Volkswagen India<br />Work : Website Design<br />Agency : 22feet Tribal Worldwide/Unpublished</p>
                </div>
              </div>
            </div>
      </div>
    </div>

    <div id="insideContent" className="vwDealershipContent">
      <div className="midWrapper">
        <div className="row">
          <ul className="one">
            <li><h3>Homepage</h3>
              <img src={vwDealershipImg1} alt="" /> </li>
              <li><h3>Model Overview</h3>
                <img src={vwDealershipImg2} alt="" /> </li>
                <li><h3>Models Detail Page</h3>
                  <img src={vwDealershipImg3} alt="" /> </li>
          </ul>
        </div>
      </div>
    </div>

    <div className="midWrapper nextPrevious textAlignCenter">
      <ul>
        <li className="back">
          <Link to="/penguin">
            <div className="thumb"> <img src={penguinImg} alt="" /> </div>
            <div className="button"><span>Back</span></div>
          </Link>
        </li>
        <li className="next">
          <Link to="/boscojack">
            <div className="thumb"> <img src={boscoImg} alt="" /> </div>
            <div className="button"><span>next</span></div>
          </Link>
        </li>
      </ul>
    </div>


  </div>
)

export default vwdealership



